body {
  padding: 60px;
}

input {
  width: 100%;
  font-size: 1.2rem;
  color: grey;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  overflow: hidden;
  border-radius: 6px;
  margin-bottom: 5px;
  margin-right: 5px;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 1700px;
}